import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from '../views/Home.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import(/* webpackChunkName: "login" */ '../views/Login.vue'),
    meta: {
      requiresAuth: false
    }
  },
  {
    path: '/empty',
    name: 'Empty',
    component: () => import(/* webpackChunkName: "empty" */ '../views/Empty.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/forms/activityForm',
    name: 'ActivityForm',
    component: () => import(/* webpackChunkName: "activityForm" */ '../views/forms/activityForm.vue'),
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/forms/patrolForm',
    name: 'PatrolForm',
    component: () => import(/* webpackChunkName: "patrolForm" */ '../views/forms/patrolForm.vue'),
    meta: {
      requiresAuth: true
    }
  }
]

const router = new VueRouter({
  routes
})
/*
router.beforeEach((to, from, next) => {
    if (to.meta.requiresAuth && !session.exists()) {
        // check if use already logged
        // if false then go to login
             return next({path:'/login'}) // '/' is home page for example
        // else then continue to next()
    }else if(!to.meta.requiresAuth && session.exists()) {
        return next({path:'/'});
    }

    return next();
});*/

export default router
