<template>
  <div id="app">
    <div v-if="this.$session.exists()" style="height: 100%;width: 100%;padding: 16px;">
      <div style="width:75%;margin-right:5%;text-align:center;margin-left:auto;">
        <vs-alert
          color="primary"
          title="Avis pour les arrivants:"
          style="width: 500px;margin-right: auto;margin-left: auto;"
          :active.sync="active1"
          closable close-icon="close">
          Merci d'être venu ici pour soutenir mon projet, ça me fait vraiment plaisir !<br>
          Pour des suggetions ou des remarques, allez sur le sujet dédié eu projet (<a href="https://www.republiqueforum.fr/t204515-avis-r-f-helper">ici</a>)
        </vs-alert>
        <transition name="fade" mode="out-in">
          <router-view/>
        </transition>
      </div>
      <SideBar/>
    </div>
    <div v-else-if="!this.$session.exists()" style="height:100%;width:100%;background-image:url('https://i.imgur.com/T01cfMc.png');background-repeat: round;">
      <router-view/>
    </div>
  </div>
</template>

<script>
import SideBar from "@/components/SideBar.vue"
import axios from 'axios'

export default {
  name: 'App',
  components: {
    SideBar
  },
  data: function() {
    return {
      active1:true
    }
  },
  mounted() {
    //this.$loadScript("@/assets/FAForm.js")
  },
  computed: {
  },
  asyncComputed: {
    accountData: function() {
      return axios.get('https://www.habbo.fr/api/public/users/' + this.$session.get("uniqueId") + '/profile')
      .then(response => {
        // JSON responses are automatically parsed.
        console.log(response)
        return response.data
      })
      .catch(e => {
        //this.$vs.notify({title:'Erreur !',text:'Une erreur s\'est produite : ' + e ,color:'danger',position:'top-center'})
        console.log(e)
      })
    }
  },
  methods: {
    logout() {
      this.$session.destroy()
      this.$router.go({ name: "Login" })
    },
    login(data) {
      this.$session.start()
      this.$session.set("uniqueId",data.uniqueId)
      //this.$router.push({ name: "Home" })
      setTimeout(function(){ window.location.replace('/') }, 1000); //Redirection after 1s
    },
    checkConnected() {
      var currentRoute = this.$router.currentRoute
      if(this.$session.exists()) {
        if(currentRoute == "/login") {
          this.$router.push({ name: "Home" })
        }
      }else{
        if(currentRoute !== "/login") {
          this.$router.push({ name: "Login" })
          //this.$vs.notify({title:'Accès refusé !',text:'Veuillez vous connecter pour avoir accès à cette partie du site',color:'danger',position:'top-center'})
        }
      }
    }
  }
}
</script>

<style>
@font-face {
  font-family: "MontserratThin";
  src: url('./assets/Montserrat-Thin.ttf');
}

html,body,#app{height:100%;}

* {
  font-family: 'Open Sans', sans-serif;
}

#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#nav {
  padding: 30px;
}

#nav a {
  font-weight: bold;
  color: #2c3e50;
}

#nav a.router-link-exact-active {
  color: #42b983;
}
</style>
